'use strict'
let timers = document.querySelectorAll(".time__num")

let deadline = new Date()
deadline.setHours(0, 14, 49, 0) // выставляем сколько минут и сек осталось. По дефолту 14 мин 49 сек

const countdown = () => {
    timers.forEach(el=>{
        // console.log(el.children[1])
        if ((deadline.getSeconds() + deadline.getMinutes() * 60) < 1) {
            el.classList.add('timeout')
            el.innerHTML = 'Время вышло'
            clearInterval(timerHandler)
        } else {
            deadline.setSeconds(deadline.getSeconds() - 1)
            el.children[0].innerHTML = deadline.getMinutes()
            el.children[1].innerHTML = deadline.getSeconds()
        }
    })

}

let timerHandler = setInterval(function () {
    countdown()
}, 1000)

window.addEventListener('DOMContentLoaded', () => {
    console.log('[ DOM loaded! ༼ つ ◕_◕ ༽つ ]')

    countdown()
});
